<template>
    <div class="backpack">
        <p>rank</p>
        <a-table :columns="columns" :data-source="rankList">
            <template #headerCell="{ column }">
                <template v-if="column.key === 'name'">
                    <span>
                        <smile-outlined />
                        Name
                    </span>
                </template>
            </template>

            <template #bodyCell="{ column, record, index, columnIndex }">
                <template v-if="column.key === 'ranking'">
                    <a> {{ index + 1 }} </a>
                </template>
                <template v-else-if="column.key === 'action'">
                    <span>123 </span>
                </template>
            </template>
        </a-table>
    </div>
</template>

<script>
import { reactive, toRefs, onMounted, ref } from "vue";
import rankApi from "@/assets/api/rankApi";
import { SmileOutlined, DownOutlined } from "@ant-design/icons-vue";

export default {
    name: "Rank", // 排行
    setup() {
        const state = reactive({
            rankList: [],
            columns: [
                {
                    title: "ranking",
                    dataIndex: "ranking",
                    key: "ranking",
                },
                {
                    title: "nickname",
                    dataIndex: "nickname",
                    key: "nickname",
                },
                {
                    title: "attackpoint",
                    dataIndex: "attackpoint",
                    key: "attackpoint",
                },
                {
                    title: "Action",
                    key: "action",
                },
            ],
        });

        onMounted(async () => {
            await getRankList();
        });

        const getRankList = async () => {
            let res = await rankApi.getList({});
            state.rankList = res.data;
        };

        return {
            ...toRefs(state),
        };
    },
};
</script>

